import React from 'react'

import { GridLayout } from '../../../atoms/GridLayout'
import { LoginPageBackground } from '../../../atoms/LoginPageBackground'
import { PageTitle } from '../../../atoms/PageTitle'
import { i18n } from '../../../i18n'
import { TPasswordLoginModel } from '../models/PasswordLoginModel'
import { PasswordLoginError } from '../molecules/PasswordLoginError'
import { PasswordResetLink } from '../molecules/PasswordResetLink'
import { PasswordLoginForm } from '../organisms/PasswordLoginForm'

interface IPasswordLoginPageProps {
  model: TPasswordLoginModel
}

function getLayout() {
  return [
    { i: 'title', x: 6, y: 2, w: 8, h: 1 },
    { i: 'loginform', x: 6, y: 3, w: 8, h: 4 },
  ]
}

export const PasswordLoginPage: React.FC<IPasswordLoginPageProps> = ({
  model,
}) => (
  <LoginPageBackground>
    <PageTitle title={i18n.t`Login page`} />
    <GridLayout layouts={getLayout()}>
      <div key="title">
        <PageTitle.Display />
      </div>
      <div key="loginform">
        <PasswordLoginForm model={model} />
        <PasswordResetLink />
      </div>
    </GridLayout>
    <PasswordLoginError model={model} />
  </LoginPageBackground>
)
