import { Trans } from '@lingui/macro'
import React from 'react'
import { Form } from 'react-form'
import styled from 'styled-components'

import { FormInputTextBox } from '../../../atoms/FormInputTextBox'
import { i18n } from '../../../i18n'
import { FormSubmit } from '../../../molecules/FormSubmit'
import { TPasswordLoginModel } from '../models/PasswordLoginModel'

interface IPasswordLoginFormProps {
  model: TPasswordLoginModel
}

const LoginContainerStyled = styled.div`
  display: inline-block;
  margin: 0.5rem;
`
const PasswordContainerStyled = styled.div`
  margin: 0.5rem;
`
const SubmitButtonContainerStyled = styled.div`
  display: inline-block;
  width: 14rem;
  margin: 0.5rem;
`

interface IFormValues {
  username: string
  password: string
}

export const PasswordLoginForm: React.FC<IPasswordLoginFormProps> = ({
  model,
}) => (
  <Form
    onSubmit={({ username, password }: IFormValues) =>
      model.requestToken(username, password)
    }
    render={formApi => (
      <form onSubmit={formApi.submitForm}>
        <LoginContainerStyled>
          <FormInputTextBox
            field="username"
            name="username"
            isRequired={true}
            validatioName={i18n.t`username`}
            autoFocus={true}
            placeholder={i18n.t`username`}
          />
        </LoginContainerStyled>

        <PasswordContainerStyled>
          <FormInputTextBox
            field="password"
            name="password"
            isRequired={true}
            validatioName={i18n.t`password`}
            type="password"
            placeholder={i18n.t`password`}
          />
        </PasswordContainerStyled>

        <SubmitButtonContainerStyled>
          <FormSubmit>
            <Trans>Sign-in</Trans>
          </FormSubmit>
        </SubmitButtonContainerStyled>
      </form>
    )}
  />
)
