import React from 'react'
import styled from 'styled-components'

import { Colors, Spacing } from '../styling'

interface INotifyMessageProps {
  isError?: boolean
  isVisible: boolean
  onClick?(): void
}

const backgroundProperty = (props: INotifyMessageProps) =>
  props.isError ? Colors.red : Colors.green

const NotifyMessageContainerStyled = styled.div`
  position: relative;
  border-radius: 1rem;
  width: 60%;
  height: 3.9rem;
  opacity: 0.85;
  left: 20%;
  background: ${backgroundProperty};
  display: flex;
  align-items: center;
  justify-content: center;
`

const NotifyMessageStyled = styled.div`
  color: white;
  text-align: center;
  margin: 0 0.5rem 0.4rem 0.5rem;
  font-size: ${Spacing.InfoMessageFontSize};
`

export const NotifyMessage: React.FC<INotifyMessageProps> = ({
  children,
  isVisible,
  isError = false,
  onClick,
  ...props
}) =>
  isVisible ? (
    <NotifyMessageContainerStyled
      onClick={onClick}
      isVisible={isVisible}
      isError={isError}
      {...props}
    >
      <NotifyMessageStyled data-testid={`notify${isError ? '-error' : ''}`}>
        {children}
      </NotifyMessageStyled>
    </NotifyMessageContainerStyled>
  ) : null
